
export function setLang(newLang) {
    if(i18n[newLang]) window.LANG = newLang;
}

export function getLangs() {
    let langs = [];
    for(let prop in i18n) {
        langs.push({key:prop, caption:i18n[prop].langCaption});
    }
    return langs;
}

export default function t(str) {
    let LANG = 'ua';
    if(window.LANG) LANG = window.LANG;
    return (i18n[LANG][str]===undefined && i18n[LANG][str]!=='') ? str : i18n[LANG][str];
}

